/**
 * @WordPress dependencies
 */
import { InnerBlocks, useBlockProps } from '@wordpress/block-editor';

/**
 * export deprecated
 */
const blockName = 'ark-block-accordion';
export default [
	// アイコン設定が子ブロック側にあった頃
	{
		supports: {
			anchor: true,
			className: false,
		},
		attributes: {},
		migrate: (attributes) => {
			const newAttrs = { ...attributes };
			newAttrs.iconType = 'plus';

			return newAttrs;
		},
		save: () => {
			const blockProps = useBlockProps.save({
				className: blockName,
			});
			return (
				<div {...blockProps}>
					<InnerBlocks.Content />
				</div>
			);
		},
	},
];
