/**
 * @WordPress dependencies
 */
import { __, _x } from '@wordpress/i18n';
import { useSelect } from '@wordpress/data';
import { registerBlockType } from '@wordpress/blocks';
import {
	InspectorControls,
	BlockControls,
	InnerBlocks,
	useBlockProps,
} from '@wordpress/block-editor';
import { PanelBody, BaseControl, ButtonGroup, Button } from '@wordpress/components';
import { useInnerBlocksProps } from '@compatible';

/**
 * @Internal dependencies
 */
import metadata from './block.json';
import example from './_example';
import blockIcon from './icon';
import deprecated from './deprecated';
import { ArkbMarginControl } from '@components';

/**
 * 設定
 */
const ICON_SETS = [
	{
		name: 'plus',
	},
	{
		name: 'caret',
	},
	{
		name: 'triangle',
	},
];

/**
 * アコーディオン
 */
const blockName = 'ark-block-accordion';
registerBlockType(metadata.name, {
	title: __('Accordion', 'arkhe-blocks'),
	description: __('Create content that can be expanded with a click.', 'arkhe-blocks'),
	icon: blockIcon,
	styles: [{ name: 'box', label: _x('Box', 'style', 'arkhe-blocks') }],
	example,
	edit: ({ attributes, setAttributes, isSelected, clientId }) => {
		const { iconType } = attributes;

		// インナーブロックが選択されているかどうか
		const isChildSelected = useSelect(
			(select) => select('core/block-editor').hasSelectedInnerBlock(clientId, false),
			[clientId]
		);

		const blockProps = useBlockProps({
			className: `${blockName} ark-has-guide ark-has-appender`,
			'data-icon-type': iconType,
		});
		const innerBlocksProps = useInnerBlocksProps(blockProps, {
			allowedBlocks: ['arkhe-blocks/accordion-item'],
			template: [['arkhe-blocks/accordion-item']],
			templateLock: false,
			renderAppender: isSelected || isChildSelected ? InnerBlocks.ButtonBlockAppender : false,
		});

		return (
			<>
				<BlockControls>
					<ArkbMarginControl attributes={attributes} setAttributes={setAttributes} />
				</BlockControls>
				<InspectorControls>
					<PanelBody title={__('Settings', 'arkhe-blocks')} initialOpen={true}>
						<BaseControl>
							<BaseControl.VisualLabel>
								{__('Icon', 'arkhe-blocks')}
							</BaseControl.VisualLabel>
							<ButtonGroup className='arkb-btns--acc'>
								{ICON_SETS.map((iconSet, idx) => {
									return (
										<Button
											key={`iconset_${idx}`}
											isPrimary={iconSet.name === iconType}
											onClick={() => {
												setAttributes({ iconType: iconSet.name });
											}}
											data-icon-type={iconSet.name}
										>
											{/* {iconSet.name} */}
											<span className='is-opened'>
												<span className={`${blockName}__icon`}></span>
											</span>
											<span>/</span>
											<span>
												<span className={`${blockName}__icon`}></span>
											</span>
										</Button>
									);
								})}
							</ButtonGroup>
						</BaseControl>
					</PanelBody>
				</InspectorControls>
				<div {...innerBlocksProps} />
			</>
		);
	},
	save: ({ attributes }) => {
		const { iconType } = attributes;
		const blockProps = useBlockProps.save({
			className: blockName,
			'data-icon-type': iconType,
		});
		return (
			<div {...blockProps}>
				<InnerBlocks.Content />
			</div>
		);
	},
	deprecated,
});
